	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/movist/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-caca9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 63.26259946949602%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"f5c06492 d1c6 415b b7fe 0b97e5b8e6b1\"\n        title=\"\"\n        src=\"/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-fb8a0.png\"\n        srcset=\"/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-1a291.png 148w,\n/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-2bc4a.png 295w,\n/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-fb8a0.png 590w,\n/static/f5c06492-d1c6-415b-b7fe-0b97e5b8e6b1-993d6bcd683c012e0c78244131d6727a-caca9.png 754w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Movist has long been one of my favourite video players for the Mac. The simplicity of it’s interface and it’s speed has kept me from using <a href=\"http://www.videolan.org\">VLC</a> or <a href=\"http://mplayerx.org\">MPlayerX</a>. I simply perfer the way Movist ‘feels’.</p>\n<p>The original Movist project is hosted on Google Code, unfortunately there hasn’t been any activity since April 2010. But being open source, Nur Monson has <a href=\"https://github.com/samiamwork/Movist/\">forked the project onto GitHub</a> and resolved the many problems with getting it to build with the latest Xcode and made it a ‘modern’ OS X application, removing reliance on depreciated APIs.</p>\n<p>Hopefully now that Nur has given the project a solid foundation again, it will once again flourish as an active open source project with an engaged community.</p>\n<p>If you want to try it out, I’ve built the latest version <a href=\"/s/Movist.zip\">which you can download here</a>.</p>","frontmatter":{"title":"Movist","date":"February 21, 2013","passthroughUrl":null}}},"pathContext":{"slug":"/blog/2013/2/movist/","previous":{"fields":{"slug":"/blog/2013/2/google-has-killed-the-android-brand/"},"frontmatter":{"title":"Google has killed the Android brand","passthroughUrl":"http://www.fabcapo.com/2013/02/google-has-killed-android-brand.html","type":null}},"next":{"fields":{"slug":"/blog/2013/2/google-chromebook-pixel/"},"frontmatter":{"title":"Google Chromebook Pixel","passthroughUrl":"http://www.google.com/intl/en/chrome/devices/chromebook-pixel/","type":null}}}}

